&__heading {
	display: none;

	@include adaptive(1023) {
		display: flex;
		align-items: center;
	}

	i {
		position: relative;
		top: 3px;
		width: 0;
		height: 0;
		margin-right: 3px;
		border-style: solid;
		border-width: 6px;
		border-color: #fff transparent transparent transparent;
	}

	span {
		color: #ffffff;
	}
}
